import React, { useState, useEffect } from "react"
import Img from "gatsby-image/withIEPolyfill"
import { useStaticQuery, graphql } from "gatsby"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import ButtonLink from "./button-link"
import styles from "./navbar.module.scss"

export default () => {
  const { avatar } = useStaticQuery(graphql`
    query {
      avatar: file(relativePath: { eq: "avatar.png" }) {
        childImageSharp {
          fixed(width: 72) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  const [isMenuActive, setIsMenuActive] = useState(false)
  const [navbarTracking, setNavbarTracking] = useState({
    scrollPosition: 0,
    isVisible: true,
  })

  const handleOnBugderButtonClick = () => {
    setIsMenuActive(prevValue => !prevValue)
  }

  useEffect(() => {
    const eventName = "scroll"
    const navbarHeight = 80
    const handleScroll = () => {
      const scrollPosition = document.body.getBoundingClientRect().top
      const isVisible =
        !scrollPosition ||
        scrollPosition >= navbarHeight * -1 ||
        scrollPosition > navbarTracking.scrollPosition
      setNavbarTracking({ scrollPosition, isVisible })
    }
    window.addEventListener(eventName, handleScroll)
    return () => {
      window.removeEventListener(eventName, handleScroll)
    }
  }, [navbarTracking.scrollPosition])

  return (
    <nav
      className={`navbar is-fixed-top ${
        navbarTracking.isVisible ? styles.navbarActive : styles.navbarHidden
      }`}
      role="navigation"
      aria-label="main navigation"
    >
      <div className="navbar-brand">
        <AniLink fade className={`navbar-item ${styles.avatarFigure}`} to="/">
          <Img fixed={avatar.childImageSharp.fixed} alt="" loading="eager" />
        </AniLink>
        <AniLink fade className="navbar-item" to="/">
          <div className={styles.logoTextContainer}>
            <strong>Hannah Nguyen</strong>
            <div>UI/UX Designer</div>
          </div>
        </AniLink>

        <button
          className={`navbar-burger burger ${styles.burgerButton} ${
            isMenuActive ? "is-active" : ""
          }`}
          aria-label="menu"
          aria-expanded="false"
          data-target="navbarBasicExample"
          onClick={handleOnBugderButtonClick}
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </button>
      </div>

      <div
        id="navbarBasicExample"
        className={`navbar-menu ${
          isMenuActive ? `is-active ${styles.menuActive}` : ""
        }`}
      >
        <div className="navbar-end">
          <AniLink
            fade
            className={`navbar-item ${styles.ctaLink}`}
            activeClassName={styles.ctaLinkHighlight}
            to="/about"
          >
            About Me
          </AniLink>
          <AniLink
            fade
            className={`navbar-item ${styles.ctaLink}`}
            activeClassName={styles.ctaLinkHighlight}
            to="/my-work"
          >
            My Work
          </AniLink>
          <ButtonLink
            to="/contact"
            title="Contact"
            wrapperClassName={`navbar-item ${styles.ctaButtonLink}`}
          />
        </div>
      </div>
    </nav>
  )
}
